import React, { Component } from 'react'
import { connect } from 'react-redux'
import '../css/card.css'
import useradd from '../store/Actions/Action_Useradds';
import {Link} from 'react-router-dom'
import Header from './header'
import {baseUrl} from '../picload'
const style = {
    upperbox: {
        textAlign: 'center'
    },
    name: {
        fontSize: '40px',
        fontWeight: '400',
        fontFamily: 'Comic Sans MS, Comic Sans, cursive'
    },
    adsArea: {
        margin: '20px',
        padding: '20px',

    }

}
const styles = {
    card: {
        maxWidth: 325

    },
    media: {
        objectFit: 'cover',
    },
    rec: {
        border: 'solid lightgrey 1px',
        borderRadius: '20px',
        backgroundColor: 'lightgrey',
        marginRight:'5px',
    }
};



class Dashboard extends Component {
    state={
        files:[]
    }
    componentDidMount = () => {
        var id = this.props.user._id;
    
        var options = {
            method: 'POST',
            body: JSON.stringify({ id }),
            headers: {
                'Content-Type': 'application/json'
            }
        }


        fetch(' /userads', options)
            .then(res => res.json())
            .then(json => {
               this.setState({files:json})
               
            })
            .catch((err)=>{console.log(err)})

    }




    render() {
        const { user } = this.props
        const { files } = this.state
     
        const addCard = files.map((ad, index) => {

            return <div className="responsive" key={index}>
                <Link to={"/detail/" + ad._id} style={{ color: 'black', textDecoration: 'none' }}>
                    <div className="gallery">

                        <img src={baseUrl+ ad.files[0].filename} alt='images' height="50" />
                        <div className="desc">
                            <div style={{fontSize:15}}>
                            <h3>{"Rs " + ad.price}</h3> {ad.location}<br />
                               
                                <b>{ad.purpose}</b><br/>
                                {ad.area + ' ' + ad.unit}<br />
                                Recomended for: <br />
                                {ad.recommendation.map((ab) => {
                                    return <span style={styles.rec} >{ab + ','}</span>
                                })}
                            </div>

                        </div>
                    </div>

                </Link>
            </div>



        })

      
        return (
            <div>

                <div style={{ backgroundColor: ' rgb(52,58,62)' }}><Header /></div>
                <div style={style.upperbox}>
                    <div style={style.name}>
                        <p>{user.name}</p>
                    </div>
                    <h4>{user.email}</h4>
                </div><hr />
                <div style={style.adsArea}>
                    <h4>Published ads:</h4>
                    <div>
                        {addCard}
                    </div>

                </div>
            </div>
        )
    }
}
const mapStateToProps = (store) => {
    return (
        {
            user: store.Reducer_User,
            files: store.Reducer_useradd
        }
    )
}

export default connect(mapStateToProps)(Dashboard)