import React from 'react';
import PropTypes from 'prop-types';

import SwipeableViews from 'react-swipeable-views';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import { TextField } from '@material-ui/core'
import green from '@material-ui/core/colors/green';
import Button from '@material-ui/core/Button';
import { connect } from 'react-redux'
import { setUser } from '../store/Actions/Action_SetUser'





function TabContainer({ children, dir }) {
    return (
        <Typography component="div" dir={dir} style={{ padding: 8 * 3 }}>
            {children}
        </Typography>
    );
}

TabContainer.propTypes = {
    children: PropTypes.node.isRequired,
    dir: PropTypes.string.isRequired,
};

const styles = theme => ({
    root: {
        backgroundColor: theme.palette.background.paper,
        width: '100%',
    },
});




class Signin extends React.Component {

    state = {
        value: 0,
        email: '',
        password: '',
        contact: '',
        newemail: '',
        newpassword: '',
        show: false,
        taken: false,
        success: false
    };

    handleChange = (event, value) => {
        this.setState({ value });
    };

    handleChangeIndex = index => {
        if (index = 0 ) {
            this.setState({ value: index });
          

        }
        if (index = 1 ) {
            this.setState({ value: index });

        }
        
    };
    handleValue = name => event => {
        this.setState({ [name]: event.target.value });
    };

    handleSubmit = (e) => {
        e.preventDefault();

        let email = this.state.email
        let password = this.state.password
        let user = {
            email,
            password
        }
      

        var options = {
            method: 'POST',
            body: JSON.stringify(user),
            headers: {
                'Content-Type': 'application/json'
            }
        }


        fetch('/signin', options)
            .then(res => res.json())
            .then(json => {
              
                this.props.dispatch(setUser(json));



            })
            .catch((error) => {
                console.log('something went wrong')
                this.setState({ show: true })
            });
    }

    handlesignup = (e) => {
        e.preventDefault();
        var email = this.state.newemail;
        var password = this.state.newpassword;
        var contact = this.state.contact;
        var name = this.state.name;
        let nUser = {
            email,
            password,
            contact,
            name
        }
        console.log(nUser);

        var options = {
            method: 'POST',
            body: JSON.stringify(nUser),
            headers: {
                'Content-Type': 'application/json'
            }

        }


        fetch('/signup', options)
            .then(res => res.json())
            .then(json => {
                if (json.taken) {
                    this.setState({ taken: true })
                }
                if (json.success) {
                    this.setState({ success: true, value: 0, taken: false })
                }

            })
            .catch((error) => { console.log(error) });

    }



    render() {
        const { classes, theme } = this.props;
        const { show, taken, success } = this.state
   


        return (
            <div className={classes.root}>
                <AppBar position="static" color="default">
                    <Tabs
                        value={this.state.value}
                        onChange={this.handleChange}
                        indicatorColor="primary"
                        textColor="primary"
                        variant="fullWidth"
                    >
                        <Tab label="SIGNIN" style={{ color: 'black', backgroundColor: 'white' }} />
                        <Tab label="SIGNUP" style={{ color: 'black', backgroundColor: 'white' }} />


                    </Tabs>
                </AppBar>
                <SwipeableViews
                    axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                    index={this.state.value}
                    // onChangeIndex={this.handleChangeIndex}
                >
                    <TabContainer dir={theme.direction}>
                        <br />
                        <br />
                        <form onSubmit={this.handleSubmit}>
                            <TextField required
                                autoFocus
                                variant="outlined"
                                name="email"
                                label="Email Address"
                                type="email"
                                fullWidth
                                value={this.state.email}
                                onChange={this.handleValue('email')}


                            /><br /><br />
                            <TextField required autoFocus variant="outlined" value={this.state.password}
                                onChange={this.handleValue('password')} name="password" label="Password" type="password" fullWidth />
                            <br />
                            {show && <p style={{ color: 'red' }}>*your email or password is incorrect</p>}
                            <br />

                            <Button
                                type="submit" fullWidth variant="contained" color="primary" style={{ color: 'white', width: '100%' }}> Sign in </Button>
                        </form>



                    </TabContainer>
                    <TabContainer dir={theme.direction}>
                        <form onSubmit={this.handlesignup}>
                            <TextField
                                required
                                autoFocus
                                variant="outlined"
                                name="name"
                                label="Name"
                                type="text"
                                fullWidth
                                value={this.state.name}
                                onChange={this.handleValue('name')}
                            /><br /><br />
                            <TextField
                                required
                                value={this.state.newemail}
                                onChange={this.handleValue('newemail')}
                                autoFocus
                                variant="outlined"
                                name="newemail"
                                label="Email Address"
                                type="email"
                                fullWidth
                            />
                            {taken && <p style={{ color: 'red' }}>already taken!</p>}
                            <br /><br />
                            <TextField
                                required
                                autoFocus
                                variant="outlined"
                                name="contact"
                                label="Contact"
                                type="number"
                                fullWidth
                                value={this.state.contact}
                                onChange={this.handleValue('contact')}
                            /><br /><br />
                            <TextField required value={this.state.newpassword}
                                onChange={this.handleValue('newpassword')} autoFocus variant="outlined" name="newpassword" label="create Password" type="password" fullWidth />
                            <br />
                            <br />
                            <br />
                            {success && <p style={{ color: 'green' }}>!Success</p>}

                            <Button
                                type="submit" fullWidth variant="contained" color="primary" style={{ color: 'white', width: '100%' }}> Sign UP </Button>
                        </form>
                    </TabContainer>

                </SwipeableViews>

            </div>
        );
    }
}


Signin.propTypes = {
    classes: PropTypes.object.isRequired,
    theme: PropTypes.object.isRequired,
};

export default connect()(withStyles(styles, { withTheme: true })(Signin));