import React, { Component } from 'react';
import { Router, Route, Switch } from "react-router-dom";
import { Provider } from 'react-redux';
import Home from './components/home';
import store from './store/store'
import history from './history'
import userAdvertise from './components/userAdvertise';
import Searches from './components/Searches'
import AdDetail from './components/Addetail';
import { setUser } from './store/Actions/Action_SetUser'
import Dashboard from './components/Dashboard'
import Footer from './components/footer';
import Rentals from './components/allRentals';

class App extends Component {


  constructor() {
    super();
    fetch('http://localhost:8000/is_authenticated', {

    }).then((res) => {
      return res.json();
    }).then((resp) => {
      if (resp) {
        // history.push('/post-ads')
        store.dispatch(setUser(resp))
        console.log(resp)
      }
      else {
        console.log('login')
      }
    }).catch((err) => console.log('not find'))
  }

  render() {



    return (
      <Provider store={store}>

        <Router history={history}>
          <div style={{display:'flex',flexDirection:'column'}}>
            <div style={{ minHeight: '550px'   }}>
              <Switch>
                <Route exact path={'/'} component={Home} />
                <Route path={'/advertise'} component={userAdvertise} />
                <Route path={'/search/ads'} component={Searches} />
                <Route path={'/detail/:_id'} component={AdDetail} />
                <Route path={'/profile'} component={Dashboard} />
              
                <Route path={'/allads'} component={Rentals} />
              




              </Switch>



            </div><br />
            <div>
              <Footer />
            </div>
          </div>
        </Router>
      </Provider>

    );
  }
}

export default App;
