import React, { Component } from 'react';
import img from '../images/_Desktop_overlay.jpg'
import Header from './header'
import '../css/hide.css'
// import SweetAlert from 'sweetalert-react';

import SearchBox from './Searchbox';

var sectionStyle = {
    backg: {
        width: '100%',
        height: 600,
        backgroundImage: `url(${img})`,
        backgroundSize: '100%'
    }
};

class Home extends Component {
  
    render() {
        return (
            <div style={sectionStyle.backg} >
                <Header />
                <center><h2  style={{color:'white', fontFamily:'Ivar Headline, Times New Roman, serif',fontSize:40}}>START YOUR OWN <br/> BUSINESS TODAY</h2>
                <p className='buttonhide' style={{color:'white',fontFamily: 'Comic Sans MS, Comic Sans, cursive'}} >WE WILL HELP YOU FIND THE PERFECT LOCATION FOR YOUR BUSINESS</p></center>
               <SearchBox/>





            </div>
        );
    }
}

export default Home;
