import React, { Component } from 'react';

import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import Header from './header'

import '../css/card.css';
import SearchBox from './Searchbox';
import {baseUrl} from '../picload'

const styles = {
    card: {
        maxWidth: 325

    },
    media: {
        objectFit: 'cover',
    },
    rec: {
        border: 'solid lightgrey 1px',
        borderRadius: '20px',
        backgroundColor: 'lightgrey',
        marginRight: '5px',
    }
};




class Searches extends Component {
    state = {
        auth: false

    }
    // componentWillReceiveProps(newprops) {
    //     if (newprops.ads.length = 0) {
    //         this.setState({ auth: false })
    //     } else {
    //         this.setState({ auth: true })
    //     }
    // }
  






    render() {
const {auth}=this.state
        const { ads } = this.props
        console.log(ads)

        const addCard = ads.map((ad, index) => {

            return <div className="responsive" key={index}>
                <Link to={"/detail/" + ad._id} style={{ color: 'black', textDecoration: 'none' }}>
                    <div className="gallery">

                        <img src={baseUrl + ad.files[0].filename} alt='images' height="50" />
                        <div className="desc">
                            <div style={{ fontSize: 15 }}>
                                <h3>{"Rs " + ad.price}</h3> {ad.location}<br />

                                <b>{ad.purpose}</b><br />
                                {ad.area + ' ' + ad.unit}<br />
                                Recomended for: <br />
                                {ad.recommendation.map((ab) => {
                                    return <span style={styles.rec} >{ab + ','}</span>
                                })}
                            </div>

                        </div>
                    </div>

                </Link>
            </div>



        })
        return (
    

            <div>
                <div style={{ backgroundColor: ' rgb(52,58,62)' }}><Header /></div>
                <SearchBox />

                <br />
                <hr />
                <center><h3>Recommendations</h3></center>
                {auth && <p style={{color:'red'}}>*no results found</p>}
                {addCard}


            </div>



        );
    }
}



const mapStateToProps = (store) => {
    return {
        ads: store.Reducer_searches
    }
}

export default connect(mapStateToProps)(withStyles(styles)(Searches));