import React, { Component } from 'react';
import { connect } from 'react-redux'
// import { setadd } from '../store/Actions/Action_Addads'
import Header from './header'
// import { Fade } from 'react-slideshow-image';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from 'react-responsive-carousel';
import '../css/theme.css'
import {baseUrl} from '../picload'




// const fadeProperties = {
//   duration: 5000,
//   transitionDuration: 500,
//   infinite: true,
//   indicators: true
// }



const style = {

  priceDetail: {
    border: '1px',
    borderColor: 'lightgrey',
    borderStyle: 'solid',
    margin: '5px',
    padding: '5px',

  },
  userDetail: {
    border: '1px',
    borderColor: 'lightgrey',
    borderStyle: 'solid',
    margin: '5px',
    padding: '5px',
    textAlign: 'center',

  },
  addDetails: {
    border: '1px',
    borderColor: 'lightgrey',
    borderStyle: 'solid',
    margin: '5px',
    padding: '5px',

  }
}

class Addetail extends Component {

  state = {
    data: {
    },
    user: {}
  }

  componentDidMount() {
    var _id = this.props.match.params._id;

    // var _id = id.toString()

    var options = {
      method: 'POST',
      body: JSON.stringify({ _id }),
      headers: {
        'Content-Type': 'application/json'
      }
    }


    fetch('/addetail', options)
      .then(res => res.json())
      .then(json => {
        this.setState({
          data: json.data,
          user: json.user
        })




      }


      )
      .catch((error) => { console.log(error) });
  }



  render() {


    const { data, user } = this.state


    let bata;
    if (data.recommendation !== undefined) {
      bata = data.recommendation.map((el, i) => {
        return <div key={i}>{el}</div>
      })

    }
    let imag;
    if (data.files !== undefined) {
      imag = data.files.map((el, i) => {

        return <div key={i} className='hidediv'>

          <img src={baseUrl + el.filename} />




        </div>

      }
      )
    }





    return (
      <div>
        <div style={{ backgroundColor: ' rgb(52,58,62)' }}><Header /></div>

        <div>
          <div className='container'>
            <div className='item0' >
              <Carousel>
                {imag}
              </Carousel>
            </div>
            <div className='item1'>
            </div>
            <div style={style.detail}>
              <div style={style.priceDetail}>
                <h4>Demand price :Rs{data.price}</h4> <hr />
                <b><h5>{data.purpose}</h5> </b>
                <b> {data.type}</b><br /><hr />
                {data.Address + ' ' + data.location}
              </div>

              <div style={style.userDetail}>
                <h3>Seller INFO</h3><hr />
                <h4> {user.name}</h4><br />
                CONTACT: <b>{user.contact}</b>
              </div>
              <div style={style.addDetails}>
                <b>Description </b><hr />
                Recommendation by the Seller:<br />
               
                <b> {bata}</b>



              </div>
            </div>





          </div>


        </div>

      </div>
   











    )
  }
}
export default connect()(Addetail);