import React, { Component } from 'react';

import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';

import { Link } from 'react-router-dom'
import history from '../history';
import Signing from './popupdialog'
import { connect } from 'react-redux'
import { setUser } from '../store/Actions/Action_SetUser'
import SideDrawer from './drawer';
import '../css/hide.css'

const styles = {
    root: {
        flexGrow: 1,
    },
    grow: {
        flexGrow: 1,
    },

};

class Header extends Component {


    state = {
        auth: false,
        open: false
    }
    componentDidMount() {
        const { user } = this.props
        if (user.length !== 0) {
            this.setState({ auth: true })
         
        }
        else {

            this.setState({ auth: false })
          
        }

    }
    all=()=>{
        history.push('/allads')
    }

    componentWillReceiveProps(newprops) {
        if (newprops.user.length !== 0) {
            this.setState({ auth: true })
         
        }
        else {

            this.setState({ auth: false })
         
        }
    }
    advertising = () => {
        if (this.state.auth) {
            history.push('/advertise')
        } else {
            alert('You must have to login first for advertising')
        }

    }
    logout = () => {
        this.props.dispatch(setUser([]));
    }

    profile = () => {
        history.push('/profile')
    }
    render() {
        const { classes } = this.props;
        const { user } = this.props
        const { auth } = this.state




        return (
            <div className={classes.root}>
                <AppBar style={{ backgroundColor: 'transparent', boxShadow: 'none' }} position="static">
                    <Toolbar>

                        <SideDrawer />

                        {/* <img src={logo} style={{height:'100px', width:'100px'}}/> */}
                        <Typography variant="h6" color="inherit" className={classes.grow}>
                            <Link to='/' style={{ textDecoration: 'none', color: 'white', fontWeight: '500' }}>AlphaShop</Link>

                        </Typography>
                        <Button className='buttonhide' color='inherit' onClick={this.all}> All ADS</Button>
                      

                        <Button className='buttonhide' color="inherit" onClick={this.advertising}> Advertise</Button>
                        {auth ? <div> <Button onClick={this.profile} className='buttonhide' color="inherit" >{user.name}</Button><Button color="inherit" onClick={this.logout} > Logout</Button></div> : <Signing open={this.state.open} />}




                    </Toolbar>
                </AppBar>
            </div>
        );
    }
}
const mapStateToProps = (store) => {
    return {
        user: store.Reducer_User
    }
}

Header.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default connect(mapStateToProps)(withStyles(styles)(Header));
