import React, { Component } from 'react';
// import addpost from '../store/Actions/Action_Addads'
import PropTypes from 'prop-types';
import classNames from 'classnames';
// import ReactChipInput from 'react-chip-input';
// import { Newad } from '../store/Actions/Action_Addads'
import { connect } from 'react-redux'
import { FormControl, OutlinedInput, Select, InputLabel, Input, withStyles, FormHelperText, Button, CssBaseline, Paper, TextField, InputAdornment } from '@material-ui/core'
import history from '../history';
import Header from './header'
import Chips from 'react-chips'





const styles = theme => ({
    main: {
        width: 'auto',
        display: 'block', // Fix IE 11 issue.
        marginLeft: theme.spacing.unit * 3,
        marginRight: theme.spacing.unit * 3,
        [theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
            width: 800,
            marginLeft: 'auto',
            marginRight: 'auto',
        },

    },
    paper: {
        marginTop: theme.spacing.unit * 8,
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        padding: `${theme.spacing.unit * 2}px ${theme.spacing.unit * 3}px ${theme.spacing.unit * 3}px`,
    },
    formControl: {
        margin: theme.spacing.unit,
        minWidth: 250,
    },
    formControlCity: {
        margin: theme.spacing.unit,
        minWidth: '80%',
    },

    form: {
        width: '80%', // Fix IE 11 issue.
        marginTop: theme.spacing.unit,
    },
    submit: {
        marginTop: theme.spacing.unit * 3,
    },
});

class Createads extends Component {
    state = {
        Address: '',
        purpose: '',
        type: '',
        chips: [],
        city: '',
        unit: '',
        price: '',
        area: '',
        files: [],
        img: null,
        pic: false,


        labelWidth: 0,
    };

    handleChange = name => event => {
        this.setState({ [name]: event.target.value });
    };

    handlefile = event => {
        this.setState({
            files: event.target.files,
            img: URL.createObjectURL(event.target.files[0])
        })
    }
   

    handleSubmit = (e) => {
        e.preventDefault()


        let purpose = this.state.purpose
        let type = this.state.type
        let recommendation = this.state.chips
        let city = this.state.city
        let unit = this.state.unit
        let price = this.state.price
        let area = this.state.area
        let files = this.state.files
        let Address = this.state.Address
        // let post = { purpose, type, recommendation, city, unit, price, area, Address }

        var data = new FormData()

        for (let i = 0; i < files.length; i++) {
            data.append('file', files[i])

        }

        data.append('purpose', purpose)
        data.append('type', type)
        for (let i = 0; i < recommendation.length; i++) {
            data.append('recommendation', recommendation[i].toLowerCase())
        }
        data.append('userid', this.props.user._id)

        data.append('city', city)
        data.append('price', price)
        data.append('area', area)
        data.append('unit', unit)
        data.append('address', Address)


        var options = {
            method: 'POST',
            body: data

        }


        fetch('/create', options)
            .then(res => res.json())
            .then((data) => {
                // this.props.dispatch(Newad(data))
                alert('Submitted Successfully');
                history.push('/')

            })
            .catch(error => alert('something went Wrong'));



    }

    onChange = chips => {
        this.setState({ chips });
    }
    








    render() {
        const { user,suggest } = this.props
       
        const { classes } = this.props;
        const { pic } = this.state
     
        return (
            <div >
                <div style={{ backgroundColor: ' rgb(52,58,62)' }}><Header /></div>
                <main className={classes.main} >
                    <Paper className={classes.paper}>
                        <CssBaseline />
                        <form className={classes.form} >
                            <FormControl required variant="outlined" className={classes.formControl}>
                                <InputLabel
                                    ref={ref => {
                                        this.InputLabelRef = ref;
                                    }}
                                    htmlFor="outlined-age-native-simple"
                                >
                                    purpose
          </InputLabel>
                                <Select
                                    native
                                    value={this.state.purpose}
                                    onChange={this.handleChange('purpose')}
                                    input={
                                        <OutlinedInput
                                            name="purpose"
                                            labelWidth={70}

                                            id="outlined-age-native-simple"
                                        />
                                    }
                                >
                                    <option value="" />
                                    <option value='For Rent'>FOR RENT</option>
                                    <option value='For Sale'>FOR SALE</option>

                                </Select>
                                <FormHelperText>Required</FormHelperText>
                            </FormControl>
                            <FormControl required variant='outlined' className={classes.formControl}>
                                <InputLabel htmlFor="type">Property-type</InputLabel>
                                <Select
                                    native
                                    value={this.state.type}
                                    onChange={this.handleChange('type')}
                                    input={
                                        <OutlinedInput
                                            name="type"
                                            labelWidth={90}
                                            id="outlined-age-native-simple"
                                        />
                                    }
                                >
                                    <option value="" />
                                    <option value='Commercial'>Commercial</option>
                                    <option value='Non-Commercial'>Non-Commercial</option>

                                </Select>
                                <FormHelperText>Required</FormHelperText>
                            </FormControl>
                            <FormControl required className={classes.formControlCity}>
                                <InputLabel htmlFor="city">City</InputLabel>
                                <Select
                                    native
                                    value={this.state.city}
                                    onChange={this.handleChange('city')}
                                    name="city"
                                    inputProps={{
                                        id: 'city',
                                    }}
                                >
                                    <option value="" />
                                    <option value='Faisalabad'>Faisalabad</option>
                                    <option value='Lahore'>lahore</option>
                                    <option value='Islamabad'>Islamabad</option>
                                    <option value='Karachi'>Karachi</option>



                                </Select>
                                <FormHelperText>Required</FormHelperText>
                            </FormControl><br />
                            <FormControl variant='outlined' className={classes.formControlCity} >
                                <InputLabel htmlFor="component-simple">Address</InputLabel>
                                <Input variant='outlined' id="component-simple" value={this.state.Address} onChange={this.handleChange('Address')} />
                            </FormControl><br /><br /><br />
                            <TextField
                                variant='outlined'
                                label="PRICE DEMAND" required
                                value={this.state.price}
                                onChange={this.handleChange('price')}
                                id="simple-start-adornment"
                                className={classNames(classes.margin, classes.formControlCity)}
                                InputProps={{
                                    startAdornment: <InputAdornment position="start">Rs:</InputAdornment>,
                                }}
                            />
                            <br /><br /><br />
                            <FormControl className={classes.formControl} >
                                <InputLabel htmlFor="component-simple">Land Area</InputLabel>
                                <Input type='number' id="component-simple" value={this.state.area} onChange={this.handleChange('area')} />
                            </FormControl>
                            <TextField

                                id="unit"
                                select
                                label="Unit"
                                className={classes.textField}
                                value={this.state.unit}
                                onChange={this.handleChange('unit')}
                                SelectProps={{
                                    native: true,
                                    MenuProps: {
                                        className: classes.menu,
                                    },
                                }}
                                helperText="Please select Unit for land Area"
                                margin="normal"
                                variant="filled"
                            >
                                <option />
                                <option value='marla'>marla</option>
                                <option value='squareMeters'>squareMeters</option>
                                <option value='squarefeet'>squarefeet</option>

                            </TextField>


                            <br />
                            <div>
                                <InputLabel htmlFor="component-simple">Recommendation for Buyer</InputLabel>
                                <Chips
                                    id="component-simple"

                                    value={this.state.chips}
                                    onChange={this.onChange}
                                    suggestions={suggest}

                                />
                            </div>
                            <br />


                            <img src={this.state.img} height='150px' width='150px' />
                            <br />
                            <br />
                            <input required
                                accept="image/*"
                                className={classes.input}
                                style={{ display: 'none' }}
                                id="raised-button-file"
                                multiple
                                type="file"
                                onChange={this.handlefile}
                            />
                            {pic && <p style={{ color: 'red' }}>*Select atleast on image</p>}
                            <label htmlFor="raised-button-file">
                                <Button variant="contained" component="span" style={{ backgroundColor: 'grey', color: 'white' }} className={classes.button}>
                                    Upload Images
                            </Button>
                                <FormHelperText>Keep tap CTRL Button for more files</FormHelperText>
                            </label>
                            <Button
                                type="submit" fullWidth variant="contained" color="primary" className={classes.submit}




                                onClick={this.handleSubmit}

                            >Submit</Button>

                        </form>
                    </Paper>

                </main>
            </div>

        )
    }
}

const mapStateToProps = (store) => {
    return {
        user: store.Reducer_User,
        suggest:store.Reducer_suggestions

    }
}
Createads.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default connect(mapStateToProps)(withStyles(styles)(Createads))