import React, { Component } from 'react';


const style = {
    main: {
        width: '100%',
        backgroundColor: 'rgb(52,58,62)',
        Color: 'white',
        height: '60px',
        position: 'relative',
        // left: 0,
        bottom: 0,
        

    },
    p: {
        paddingTop: '20px',
        color: 'white'

    }
}

class Footer extends Component {
    render() {
        return (
            <div style={style.main}>
                <center> <p style={style.p}>
                    CopyRight &copy; 2019 <b>AlphaShop</b>
                </p></center>
            </div>
        )
    }
}
export default Footer;
