let INIT_state = [

]

const Reducer_ads = (state = INIT_state, action) => {
  switch (action.type) {
    case "ADD_ADS":
      return action.payload
    case "NEW_ADS":
      return [action.payload, ...state]
    
    
   
      default:
      return state
    

  }
}

export default Reducer_ads;