import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import { connect } from 'react-redux'
import history from '../history'
import { setUser } from '../store/Actions/Action_SetUser'
import Signing from './popupdialog'



const styles = {
    list: {
        width: 220,
    },

    menuButton: {
        marginLeft: -12,
        marginRight: 20,
    },
};
class SideDrawer extends React.Component {
    state = {
        left: false,
        auth: false,
        open: false
    };

    toggleDrawer = (side, open) => () => {
        this.setState({
            [side]: open,
        });
    };
    componentDidMount() {
        const { user } = this.props
        if (user.length !== 0) {
            this.setState({ auth: true })
        
        }
        else {

            this.setState({ auth: false })
        
        }

    }

    componentWillReceiveProps(newprops) {
        if (newprops.user.length !== 0) {
            this.setState({ auth: true })
          
        }
        else {

            this.setState({ auth: false })
          
        }
    }
    advertising = () => {
        if (this.state.auth) {
            history.push('/advertise')
        } else {
            alert('You must have to login first for advertising')
        }

    }
    all = () => {
        history.push('/allads')
    }
    logout = () => {
        this.props.dispatch(setUser([]));
    }

    profile = () => {
        history.push('/profile')
    }
    advertising = () => {
        if (this.state.auth) {
            history.push('/advertise')
        } else {
            alert('You must have to login first for advertising')
        }

    }
    render() {
        const { classes } = this.props;
        const { user } = this.props
        const { auth } = this.state


        const sideList = (
            <div className={classes.list}>
                <List>
                    

                    <ListItem button onClick={this.advertising}>
                        <ListItemText primary='Advertise' />


                    </ListItem>

                    <ListItem button onClick={this.all}>

                        <ListItemText primary='All ADS' />


                    </ListItem>
                    {auth ? <div> <ListItem button onClick={this.profile} color="inherit" >My Account </ListItem> <ListItem button color="inherit" onClick={this.logout} > Logout</ListItem></div> : <p></p>}



                </List>

            </div>
        );


        return (
            <div>
                <IconButton onClick={this.toggleDrawer('left', true)} className={classes.menuButton} color="inherit" aria-label="Menu">
                    <MenuIcon />
                </IconButton>
                <Drawer open={this.state.left} onClose={this.toggleDrawer('left', false)}>
                    <div
                        tabIndex={0}
                        role="button"
                        onClick={this.toggleDrawer('left', false)}
                        onKeyDown={this.toggleDrawer('left', false)}
                    >
                        {sideList}
                    </div>
                </Drawer>
            </div>
        );
    }
}

SideDrawer.propTypes = {
    classes: PropTypes.object.isRequired,
};
const mapStateToProps = (store) => {
    return {
        user: store.Reducer_User
    }
}

export default connect(mapStateToProps)(withStyles(styles)(SideDrawer));