import React, { Component } from 'react'
import '../css/main.css'
import addsearches from '../store/Actions/Action_Searches'
import { connect } from 'react-redux'
import history from '../history';
import Autosuggest from 'react-autosuggest';
import suggests from '../store/Actions/Action_Suggestions';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
// import theme from '../css/theme.css'
// import { defaultTheme } from 'react-autosuggest/dist/theme';
import CircularProgress from '@material-ui/core/CircularProgress';
const theme = {


    ...{
        container: {
            // ...defaultTheme.container,
            display: 'visible',
            width: '100%',
            height: '100%'


        },
        input: {
            // ...defaultTheme.input,

            width: '100%',
            height: '100%'

        }, suggestionsContainer: {
            display: 'none',

        },
        suggestionsContainerOpen: {
            display: 'block',
            position: 'absolute',
            // top: '51px',
            width: '280px',

            border: '1px solid #aaa',
            backgroundColor: '#fff',
            fontFamily: 'Helvetica, sans-serif',
            fontWeight: 300,
            fontSize: '16px',
            borderBottomLeftRadius: '4px',
            borderBottomRightRadius: '4px',
            zIndex: 2,


        },
        suggestionsList: {
            margin: 0,
            padding: 0,
            listStyleType: 'none',
        }, suggestion: {
            cursor: 'pointer',
            padding: '10px 20px',

        }, suggestionHighlighted: {
            backgroundColor: '#ddd',


        },


    }
}
const styles = theme => ({
    progress: {
        margin: theme.spacing.unit * 2,
    },
});


// Teach Autosuggest how to calculate suggestions for any given input value.


// When suggestion is clicked, Autosuggest needs to populate the input
// based on the clicked suggestion. Teach Autosuggest how to calculate the
// input value for every given suggestion.

class SearchBox extends Component {
    state = {
        city: '',

        auth: false,
        value: '',
        suggestions: [],
        sugg: [],
        progress: false

    }
    getSuggestions = value => {
        const inputValue = value.trim().toLowerCase();
        const inputLength = inputValue.length;

        return inputLength === 0 ? [] : this.state.sugg.filter(lang =>
            lang.name.toLowerCase().slice(0, inputLength) === inputValue
        );
    };
    getSuggestionValue = suggestion => suggestion.name;

    // Use your imagination to render suggestions.
    renderSuggestion = suggestion => (
        <div>
            {suggestion.name}
        </div>
    );

    handleChange = name => event => {
        this.setState({ [name]: event.target.value })


    }
    handleSubmit = (e) => {
        this.setState({ progress: true })
        e.preventDefault();
        let city = this.state.city
        let a = this.state.value
        let prefer = a.toLowerCase();

        let search = { city, prefer }

        var options = {
            method: 'POST',
            body: JSON.stringify(search),
            headers: {
                'Content-Type': 'application/json'
            }
        }


        fetch(' /searching', options)
            .then(res => res.json())
            .then(json => {

                this.props.dispatch(addsearches(json))
                this.setState({ progress: false })
                history.push('/search/ads')
            })


            .catch(error => {
                console.error('Error:', error)
                this.setState({ progress: false })
            });


    }
    onChange = (event, { newValue }) => {
        this.setState({
            value: newValue
        });
    };

    // Autosuggest will call this function every time you need to update suggestions.
    // You already implemented this logic above, so just use it.
    onSuggestionsFetchRequested = ({ value }) => {
        this.setState({
            suggestions: this.getSuggestions(value)
        });
    };

    // Autosuggest will call this function every time you need to clear suggestions.
    onSuggestionsClearRequested = () => {
        this.setState({
            suggestions: []
        });
    };
    componentDidMount() {

        fetch('/suggestions', {

        }).then((res) => {
            return res.json();
        }).then((resp) => {
            if (resp) {
                this.setState({ sugg: resp })
                let u = [];
                for (var i = 0; i < resp.length; i++) {
                    let obj = resp[i].name
                    u.push(obj)
                    this.props.dispatch(suggests(u))
                }
            }
            // history.push('/post-ads')

            else {
                console.log('no suggests')
            }
        }).catch((err) => console.log('not find'))
    }





    render() {
        const { value, suggestions, progress } = this.state;
        const { classes } = this.props


        // Autosuggest will pass through all these props to the input.
        const inputProps = {
            placeholder: 'Your preferred Business Description(e.g property broker)',
            value,
            onChange: this.onChange
        };
        return (
            <div>

                <div className="s003" >

                    <form onSubmit={this.handleSubmit}>
                        <div className="inner-form" >
                            <div className="input-field first-wrap">
                                <div className="input-select">
                                    <select required value={this.state.city} onChange={this.handleChange('city')} style={{ width: '100%', height: '65px' }} name="choices-single-defaul">
                                        <option placeholder="">City</option>
                                        <option value="Faisalabad">Faisalabad</option>
                                        <option value="Lahore">Lahore</option>
                                        <option value='Islamabad'>Islamabad</option>
                                        <option value='Karachi'>Karachi</option>

                                    </select>
                                </div>
                            </div>

                            <div className="input-field second-wrap">
                                <Autosuggest theme={theme}
                                    suggestions={suggestions}
                                    onSuggestionsFetchRequested={this.onSuggestionsFetchRequested}
                                    onSuggestionsClearRequested={this.onSuggestionsClearRequested}
                                    getSuggestionValue={this.getSuggestionValue}
                                    renderSuggestion={this.renderSuggestion}
                                    inputProps={inputProps}
                                />


                                {/* <input id="search" type="text" required name='prefer' value={this.state.prefer} onChange={this.handleChange('prefer')} placeholder="Enter your Business description e.g coffee shop" /> */}
                                {/* <FormHelperText ><b style={{ color: 'white' }}>Your preferred Business Description</b></FormHelperText> */}
                            </div>
                            <div className="input-field third-wrap">
                                <button style={{ backgroundColor: 'lightgreen', color: 'black' }} className="btn-search" type="submit">
                                    Search
                    </button>
                            </div>
                        </div>
                    </form>

                </div>
                <center>
                    {progress && <CircularProgress className={classes.progress} />}
                </center>
            </div>
        )
    }
}
const mapStateTOProps = (store) => {
    return {
        suggest: store.Reducer_suggestions
    }
}
SearchBox.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default connect(mapStateTOProps)(withStyles(styles)(SearchBox));