import React, { Component } from 'react';

import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import Header from './header'
import { addpost } from '../store/Actions/Action_Addads'
import '../css/card.css';
import SearchBox from './Searchbox';
import {baseUrl} from '../picload'


const styles = {
    card: {
        maxWidth: 325

    },
    media: {
        objectFit: 'cover',
    },
    rec: {
        border: 'solid lightgrey 1px',
        borderRadius: '20px',
        backgroundColor: 'lightgrey',
        marginRight: '5px',
    }
};




class Rentals extends Component {
    state = {
        ads: [],

    }

    componentDidMount() {
        fetch('/allrentals')
            .then(res => res.json())
            .then(json => {

                this.setState({ ads: json })
                this.props.dispatch(addpost(json))

            })


            .catch(error => console.error('Error:', error));
    }







    render() {

        const { ads } = this.props
      

            const addCard = ads.map((ad, index) => {
             

                return <div className="responsive" key={index}>
                    <Link to={"/detail/" + ad._id} style={{ color: 'black', textDecoration: 'none' }}>
                        <div className="gallery">

                            <img src={baseUrl+ad.files[0].filename} alt='images' height="50" />
                            <div className="desc">
                                <div style={{ fontSize: 15 }}>
                                    <h3>{"Rs " + ad.price}</h3> {ad.location}<br />

                                    <b>{ad.purpose}</b><br />
                                    {ad.area + ' ' + ad.unit}<br />
                                    Recomended for: <br />
                                    {ad.recommendation.map((ab) => {
                                        return <span style={styles.rec} >{ab + ','}</span>
                                    })}
                                </div>

                            </div>
                        </div>

                    </Link>
                </div>



            })
       
        return (

            <div>
                <div style={{ backgroundColor: ' rgb(52,58,62)' }}><Header /></div>
                <SearchBox />


                <br />
                <hr />
                <center><h3>All Rentals</h3></center>
                {addCard}


            </div>



        );
    }
}



const mapStateToProps = (store) => {
    return {
        ads: store.Reducer_ads
    }
}

export default connect(mapStateToProps)(withStyles(styles)(Rentals));